import React, { useContext, useEffect, useState } from 'react';
import Fade from 'react-reveal/Fade';
import Tilt from 'react-tilt';
import { Container, Row, Col } from 'react-bootstrap';
import PortfolioContext from '../../context/context';
import Title from '../Title/Title';
import ProjectImg from '../Image/ProjectImg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faExternalLinkAlt } from '@fortawesome/free-solid-svg-icons'

const OpenSource = () => {
  const { opensource } = useContext(PortfolioContext);

  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="projects">
      <Container>
        <div className="project-wrapper">
          <Title title="Open Source Contributions" />
          {opensource.map((opensources) => {
            const { title, info, info2, url, repo, id } = opensource;

            return (
                  <Fade
                    left={isDesktop}
                    bottom={isMobile}
                    duration={1000}
                    delay={500}
                    distance="30px"
                  >
                    <div className="project-wrapper__text">
                      <h3 className="project-wrapper__text-title"><h1><u>Organization:</u> <a className="anchor_increase_font" href="https://dvc.org/">DVC</a></h1></h3>
                      <div>
                        <p>
                          <b>Description:</b> DVC is an organization which offers a version control system for developing Machine Learning projects. It handles huge datasets and has workflow similar to <b>git</b>. My contribution is to the documentation part of the organization.
                        </p>
                      </div>
                      <br></br>
                      <h3 className="project-wrapper__text-title"><h1><u>Organization:</u> <a className="anchor_increase_font" href="https://matplotlib.org/">Matplotlib</a></h1></h3>
                      <div>
                        <p>
                          <b>Description:</b> Matplotlib is a library which helps to perform data visualizations. These visualizations can be static as well as animated. 3D visualizations are also supported. Added a PR for introducing the tutorial for <b>autoscaling</b> feature.
                        </p>
                      </div>
                      <br></br>
                      <h3 className="project-wrapper__text-title"><h1><u>Organization:</u> <a className="anchor_increase_font" href="https://opensource.facebook.com/">Facebook Research</a></h1></h3>
                      <div>
                        <p>
                          <b>Description:</b> Facebook Research is an organization maintained by researchers and developers at <b>Facebook</b> to enhance or speed up the work in the field of Artificial Intelligence.. My contribution is to the project <b><a className="anchor_show_link" href="https://github.com/facebookresearch/detectron2">detectron2</a></b> of this organization.
                        </p>
                      </div>
                    </div>
                  </Fade>
          );
        })}
        </div>
      </Container>
    </section>
  );
};

export default OpenSource;
